.yd-preview-item{padding:0 .24rem;font-size:.27rem;color:#999;display:-webkit-box;display:-ms-flexbox;display:flex;padding-bottom:.15rem}
.yd-preview-item>*{display:block}
.yd-preview-item>:first-child{min-width:1.1rem;text-align-last:justify;margin-right:.4rem;height:100%;text-align:right}
.yd-preview-item>:last-child{word-break:normal;word-wrap:break-word;text-align:right;line-height:.45rem;-webkit-box-flex:1;-ms-flex:1;flex:1}
.yd-preview-item:last-child{padding-bottom:.25rem}
.yd-preview-footer{height:1rem;-ms-flex-align:center;font-size:.28rem;color:#999;margin-top:.1rem}
.yd-preview-footer,.yd-preview-footer>*{position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center}
.yd-preview-footer>*{-webkit-box-flex:1;-ms-flex:1;flex:1;height:inherit;-ms-flex-align:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center}
.yd-preview-footer>:not(:first-child):after{content:"";position:absolute;z-index:0;top:0;left:0;width:1px;height:100%;background-image:-webkit-gradient(linear,right top, left top,color-stop(50%, #ececec),color-stop(0, transparent));background-image:linear-gradient(270deg,#ececec 50%,transparent 0)}
.yd-preview-footer:before{content:"";position:absolute;z-index:0;top:-1px;left:0;width:100%;height:1px;background-image:-webkit-gradient(linear,left bottom, left top,color-stop(50%, #ececec),color-stop(0, transparent));background-image:linear-gradient(0deg,#ececec 50%,transparent 0)}